"use client"

import { useEffect, useRef, useState } from "react"

import { cn } from "@/lib/utils"

interface InfrastructureCard {
  title: string
  description?: string
  gradient: [string, string]
  align?: "center"
}

const infrastructureCards: InfrastructureCard[] = [
  {
    title: "3D Printing",
    description: "High-precision FDM and resin rigs for rapid iteration day or night.",
    gradient: ["#5B3FFF", "#6B63FF"],
  },
  {
    title: "Laser Cutting",
    description: "Dual laser beds tuned for acrylic, ply, and composite panels with safety marshals on shift.",
    gradient: ["#FF2D8D", "#FF5A7A"],
  },
  {
    title: "Metal Cutting",
    description: "CNC saws and finishing bays ready for chassis work with trained fabrication support.",
    gradient: ["#36C3FF", "#6FF9C2"],
  },
  {
    title: "All the best",
    gradient: ["#002449", "#002449"],
    align: "center",
  },
]

export function InfrastructureScrollStack() {
  const cardRefs = useRef<(HTMLDivElement | null)[]>([])
  const [activeIndex, setActiveIndex] = useState(0)

  useEffect(() => {
    const nodes = cardRefs.current.filter((node): node is HTMLDivElement => Boolean(node))
    if (!nodes.length) return undefined

    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            const index = Number((entry.target as HTMLElement).dataset.index)
            if (!Number.isNaN(index)) {
              setActiveIndex(index)
            }
          }
        })
      },
      {
        rootMargin: "-45% 0px -35% 0px",
        threshold: 0.2,
      },
    )

    nodes.forEach((node) => observer.observe(node))

    return () => {
      observer.disconnect()
    }
  }, [])

  const baseTopRem = 8
  const layerOffsetRem = 4.2
  const overlapRem = 11

  return (
    <section className="w-full border-t border-[#d9e2f2] bg-white px-6 py-16">
      <div className="mx-auto flex w-full max-w-5xl flex-col gap-10">
        <header className="text-center">
          <p className="text-xs font-semibold uppercase tracking-[0.4em] text-[#7b859c]">Infrastructure at GITAM</p>
          <h2 className="mt-2 text-3xl font-bold text-[#002449]">Scroll Through the Prototyping Stack</h2>
          <p className="mt-3 text-sm text-[#3f4a63]">
            Glide through the fabrication hubs without lag—each card layers smoothly as you scroll down the timeline.
          </p>
        </header>

        <div className="relative mt-8 pb-[22rem]">
          {infrastructureCards.map((card, index) => {
            const top = baseTopRem + index * layerOffsetRem
            const marginTop = index === 0 ? "0rem" : `-${overlapRem}rem`
            const isActive = index === activeIndex
            const [from, to] = card.gradient

            return (
              <article
                key={card.title}
                ref={(node) => {
                  cardRefs.current[index] = node instanceof HTMLDivElement ? node : null
                }}
                data-index={index}
                style={{
                  top: `${top}rem`,
                  marginTop,
                  zIndex: infrastructureCards.length - index,
                  backgroundImage: `linear-gradient(135deg, ${from}, ${to})`,
                  transformStyle: "preserve-3d",
                  backfaceVisibility: "hidden",
                }}
                className={cn(
                  "sticky mx-auto flex w-full max-w-[720px] flex-col rounded-[32px] px-8 py-10 text-white shadow-[0_24px_60px_rgba(0,0,0,0.14)] transition-all duration-500",
                  "will-change-transform",
                  isActive
                    ? "translate-y-0 scale-[1.02] shadow-[0_32px_90px_rgba(0,0,0,0.18)]"
                    : "translate-y-2 scale-[0.98] opacity-90",
                  card.align === "center"
                    ? "items-center justify-center text-center"
                    : "gap-4 justify-between",
                )}
              >
                {card.align === "center" ? (
                  <h3 className="text-4xl font-semibold tracking-wide">{card.title}</h3>
                ) : (
                  <>
                    <span className="text-[11px] font-semibold uppercase tracking-[0.38em] text-white/75">Fabrication Bay</span>
                    <h3 className="text-3xl font-semibold">{card.title}</h3>
                    <p className="text-sm text-white/90">{card.description}</p>
                  </>
                )}
              </article>
            )
          })}
        </div>
      </div>
    </section>
  )
}
